<template>
  <b-container fluid>
    <b-card class="mx-auto shadow" title="로그인" style="max-width: 40rem;">
      <b-card-body>
        <b-form @submit.prevent="onSubmit">
          <b-form-group id="username-group" label-for="username" :state="success">
              <b-form-input type="text" id="username" v-model="username" placeholder="아이디" required></b-form-input>
          </b-form-group>
          <b-form-group id="password-group" label-for="password" :state="success" invalid-feedback="사용자 이름 또는 비밀번호가 일치하지 않습니다.">
              <b-form-input type="password" id="password" v-model="password" placeholder="비밀번호" required></b-form-input>
          </b-form-group>
          <b-form-group id="button-group">
            <b-button type="submit" block variant="primary">로그인</b-button>
          </b-form-group>
          <router-link to='/signup'>회원가입</router-link>
          </b-form>
        </b-card-body>
    </b-card>
  </b-container>
</template>

<script>
import axios from 'axios'

export default {
  name: 'SignIn',
  data () {
    return {
      username: '',
      password: '',
      success: true
    }
  },
  methods: {
    onSubmit () {
      var data = {
        username: this.username,
        password: this.password
      }
      axios.post('/api/auth/signin', data)
        .then(res => {
          this.success = res.data
          if (this.success) {
            this.$router.push('/')
          }
        })
        .catch(err => console.log(err))
    }
  }
}
</script>

<style>
</style>



