<template>
  <div class="container">
    <b-card class="mx-auto my-auto shadow" title="회원가입" style="max-width: 40rem;">
      <b-card-body>
        <b-form @submit.prevent="onSubmit">
          <b-form-group id="username-group" label-for="username" :state="usernameState" :invalid-feedback="invalidUsernameFeedback">
            <b-form-input type="text" id="username" v-model="username" placeholder="사용자 이름" :state="usernameState" @focus="onInit" @blur="onCheckDuplicate" required></b-form-input>
          </b-form-group>
          <b-form-group id="password-group" label-for="password" :state="passwordState" invalid-feedback="대/소문자, 숫자, 특수문자 포함하여 8글자 이상 입력하세요.">
            <b-form-input type="password" id="password" v-model="password" placeholder="비밀번호" :state="passwordState" required></b-form-input>
          </b-form-group>
          <b-form-group id="comfirm-group" label-for="comfirm" :state="confirmState" invalid-feedback="비밀번호가 일치하지 않습니다.">
            <b-form-input type="password" id="comfirm" v-model="confirm" placeholder="비밀번호 학인" :state="confirmState" required></b-form-input>
          </b-form-group>
          <b-form-group id="name-group" label-for="name">
            <b-form-input type="text" id="name" v-model="name" placeholder="이름" required></b-form-input>
          </b-form-group>
          <b-form-group id="birth-group" label-for="birth">
            <b-form-input type="date" id="birth" v-model="birth" placeholder="생년월일" required></b-form-input>
          </b-form-group>
          <b-button type="submit" block variant="primary">가입하기</b-button>
        </b-form>
      </b-card-body>
    </b-card>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'SignUp',
  data () {
    return {
      username: '',
      password: '',
      confirm: '',
      name: '',
      birth: '',
      available_username: true
    }
  },
  methods: {
    onInit () {
      this.available_username = true
    },
    onCheckDuplicate (event) {
      var data = {
        username: event.target.value
      }
      if (data.username.length >= 6) {
        axios.get('http://121.186.161.219/api/auth/check_duplicate', {
          params: { username: event.target.value }
        })
          .then(res => {
            this.available_username = !res.data
          })
          .catch(err => {
            console.log(err)
          })
      }
    },
    onSubmit () {
      var data = {
        username: this.username,
        password: this.password
      }
      axios.post('/api/auth/signup', data)
        .then(res => {
          console.log(res)
        })
        .catch(err => {
          console.log(err)
        })
    }
  },
  computed: {
    usernameState () {
      return this.username.length >= 6 && this.available_username
    },
    invalidUsernameFeedback () {
      if (this.username.length < 6) {
        return '6글자 이상 입력해주세요.'
      } else if (!this.available_username) {
        return '이미 사용 중인 사용자 이름입니다.'
      }
    },
    passwordState () {
      return /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[$@$!%*?&])[A-Za-z\d$@$!%*?&]{8,}$/.test(this.password)
    },
    confirmState () {
      if (this.password === '') {
        return null
      }
      return this.password === this.confirm
    }
  }
}
</script>

<style>
</style>



